import { Injectable } from '@angular/core';
import { Platform } from 'ionic-angular';
import { SQLite, SQLiteObject } from '@ionic-native/sqlite';
import { SQLitePorter } from '@ionic-native/sqlite-porter';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { BehaviorSubject } from 'rxjs/Rx';
import { Storage } from '@ionic/storage';
 
@Injectable()
export class DatabaseProvider {
  database: SQLiteObject;
  private databaseReady: BehaviorSubject<boolean>;
 
  constructor(public sqlitePorter: SQLitePorter, private storage: Storage, private sqlite: SQLite, private platform: Platform, private http: Http) {
    this.databaseReady = new BehaviorSubject(false);
    this.platform.ready().then(() => {
      this.sqlite.create({
        name: 'mypos.db',
        location: 'default'
      })
        .then((db: SQLiteObject) => {
          this.database = db;
          this.storage.get('database_filled').then(val => {
            if (val) {
              this.databaseReady.next(true);
            } else {
              this.fillDatabase();
            }
          });
        });
    });
  }
 
  fillDatabase() {
    this.http.get('assets/dummyDump.sql')
      .map(res => res.text())
      .subscribe(sql => {
        this.sqlitePorter.importSqlToDb(this.database, sql)
          .then(data => {
            this.databaseReady.next(true);
            this.storage.set('database_filled', true);
          })
          .catch(e => console.error(e));
      });
  }
 
  addDeveloper(name, skill, years) {
    let data = [name, skill, years]
    return this.database.executeSql("INSERT INTO developer (name, skill, yearsOfExperience) VALUES (?, ?, ?)", data).then(data => {
      return data;
    }, err => {
      console.log('Error: ', err);
      return err;
    });
  }
 
  getAllDevelopers() {
    return this.database.executeSql("SELECT * FROM developer", []).then((data) => {
      let developers = [];
      if (data.rows.length > 0) {
        for (var i = 0; i < data.rows.length; i++) {
          developers.push({ name: data.rows.item(i).name, skill: data.rows.item(i).skill, yearsOfExperience: data.rows.item(i).yearsOfExperience });
        }
      }
      return developers;
    }, err => {
      console.log('Error: ', err);
      return [];
    });
  }

  addItemsModify(id_real, name, image, category_id) {
    let data = [id_real, name, image, category_id]
    return this.database.executeSql("INSERT INTO itemsmodify (id_real, name, image, category_id) VALUES (?, ?, ?, ?)", data).then(data => {
      console.log('berhasil');
    }, err => {
      console.log('Error: ', err);
      return err;
    });
  }

  updateCategoryItemsModify(category_id) {
    let data = [category_id]
    return this.database.executeSql("UPDATE itemsmodify set category_id=?", data).then(data => {
      console.log('berhasil');
    }, err => {
      console.log('Error: ', err);
      return err;
    });
  }

  getLastItemId(){
    return this.database.executeSql("SELECT max(id) as maxid FROM itemsmodify", {}).then((data) =>{
      return data.rows.item(0).maxid;
    });
  }

  deleteItemsModify(ids) {
    return this.database.executeSql("DELETE FROM itemsmodify WHERE id in (?)", ids).then(() => {
      console.log('berhasil hapus');
    }, err => {
      console.log('Error: ', err);
    });
  }

  getAllItemsModify() {
    return this.database.executeSql("SELECT * FROM itemsmodify", []).then((data) => {
      let items = [];
      if (data.rows.length > 0) {
        for (var i = 0; i < data.rows.length; i++) {
            items.push({ 
                id: data.rows.item(i).id,
                id_real: data.rows.item(i).id_real,
                name: data.rows.item(i).name, 
                image: data.rows.item(i).image, 
                category_id: data.rows.item(i).category_id });
        }
      }
      return items;
    }, err => {
      console.log('Error: ', err);
      return [];
    });
  }

  addPriceVariantsModify(name, price, sku, barcode, image, itemId) {
    let data = [name, price, sku, barcode, image, itemId]
    return this.database.executeSql("INSERT INTO pricevariantsmodify (name, price, sku, barcode, image, itemId) VALUES (?, ?, ?, ?, ?, ?)", data).then(data => {
      return data;
    }, err => {
      console.log('Error: ', err);
      return err;
    });
  }

  deletePriceVariantsModify(ids) {
    return this.database.executeSql("DELETE FROM pricevariantsmodify WHERE id in (?)", ids).then(() => {
      console.log('berhasil hapus');
    }, err => {
      console.log('Error: ', err);
    });
  }

  getAllPriceVariantsModify() {
    return this.database.executeSql("SELECT * FROM pricevariantsmodify", []).then((data) => {
      let pricevariants = [];
      if (data.rows.length > 0) {
        for (var i = 0; i < data.rows.length; i++) {
            pricevariants.push({ 
                id: data.rows.item(i).id,
                name: data.rows.item(i).name, 
                price: data.rows.item(i).price, 
                sku: data.rows.item(i).sku, 
                barcode: data.rows.item(i).barcode, 
                image: data.rows.item(i).image, 
                item_id: data.rows.item(i).itemId });
        }
      }
      return pricevariants;
    }, err => {
      console.log('Error: ', err);
      return [];
    });
  }

  truncatePriceVariantsModify(){
      this.database.executeSql('DROP TABLE pricevariantsmodify', {}).then(()=>{console.log('ok')});
      this.database.executeSql('CREATE TABLE IF NOT EXISTS pricevariantsmodify(id INTEGER PRIMARY KEY AUTOINCREMENT,name TEXT,price TEXT,sku TEXT,barcode TEXT,image TEXT,itemId TEXT)', {}).then(()=>{console.log('ok')});
  }
  truncateItemsModify(){
      this.database.executeSql('DROP TABLE itemsmodify', {}).then(()=>{console.log('ok')});
      this.database.executeSql('CREATE table IF NOT EXISTS itemsmodify(id INTEGER PRIMARY KEY AUTOINCREMENT, id_real INTEGER, name TEXT, image TEXT, category_id INTEGER)', {}).then(()=>{console.log('ok')});
  }
 
  getDatabaseState() {
    return this.databaseReady.asObservable();
  }
 
}


